<template>
  <div id="quick-start">
    v-once
    <span v-once>{{ message }}</span>
    <br>
    {{ message }}
    <br>
    
    <input
      v-model="message"
      type="text">
    <br>
    
    <input
      v-model.lazy="message"
      type="text">
    <br>
    <button @click="reverseMessage">
      
    </button>
    <br><br>
    <span :title="mouseMessage">
      
    </span>
    <br><br>
    v-if
    <span v-if="seen"></span>
    <br>
    <button @click="seen = !seen">
      {{ seen === true ? '' : '' }}
    </button>
    <br><br>
    <ol>
      <li
        v-for="todo in todoLists"
        :key="todo.id">
        {{ todo.text }}
      </li>
    </ol>
    <br><br>
    <button @click="warn('Form cannot be submitted yet.', $event)">
      Submit
    </button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      message: 'Hello Vue!',
      mouseMessage: '' + new Date().toLocaleString(),
      seen: false,
      todoLists: [
        { text: 'JeffBee', id: 1 },
        { text: 'Agenric', id: 2 },
        { text: 'Target', id: 3 }
      ]

    }
  },
  methods: {
    reverseMessage: function () {
      this.message = this.message
        .split(' ')
        .reverse()
        .join(' ')
    },
    warn: function (message, event) {
      // 
      if (event) event.preventDefault()
      alert(message)
    }
  }
}
</script>
